import * as React from 'react';
import { useTranslation } from 'react-i18next';
import { CoreAppLink } from './CoreAppLink';

export interface CoreMenuItemsProps {
  isMobile?: boolean;
}

const CoreMenuItems: React.FunctionComponent<CoreMenuItemsProps> = ({
  isMobile,
}: CoreMenuItemsProps) => {
  const { t } = useTranslation(['common', 'users']);
  return (
    <>
      <CoreAppLink
        to="/"
        label={t('Home')}
        activeOnlyWhenExact
        isMobile={isMobile}
      />
      <CoreAppLink
        to="/users"
        label={t('users:Title')}
        activeOnlyWhenExact
        isMobile={isMobile}
      />
      <CoreAppLink
        to="/tenants"
        label={t('tenants:Title')}
        activeOnlyWhenExact
        isMobile={isMobile}
      />
    </>
  );
};

export default CoreMenuItems;
