import React, { JSXElementConstructor, ReactElement } from 'react';
import tw from 'tailwind-styled-components';
import {RadioGroupProps} from './RadioGroupProps';

const RadioGroupContainer = tw.div`
flex
flex-col
`;

const RadioGroup: React.FC<RadioGroupProps> = ({
  name,
  value,
  onChangeGroup,
  children,
}) => {
  const childrenExtended = React.Children.map(children, (child) => {
    const narrowedChild = child as ReactElement<
      any,
      string | JSXElementConstructor<any>
    >;

    return React.cloneElement(narrowedChild, {
      name,
      onChange: onChangeGroup,
      checked: narrowedChild.props.value === value,
    });
  });

  return <RadioGroupContainer>{childrenExtended}</RadioGroupContainer>;
};

export default RadioGroup;
