import * as React from 'react';

interface SvgFilterWrapperProps {
  className?: string;
  viewBox?: string;
}

const SvgFilterWrapper: React.FunctionComponent<SvgFilterWrapperProps> = ({
  children,
  className = 'h-5 w-5 inline-block',
  viewBox = '0 0 16 16',
}) => (
  <svg
    className={className}
    xmlns="http://www.w3.org/2000/svg"
    viewBox={viewBox}
    fill="currentColor"
  >
    {children}
  </svg>
);

export default SvgFilterWrapper;
