import { types, Instance, flow } from 'mobx-state-tree';

export const CurrentUser = types.model({
  username: types.string,
  firstName: types.string,
  lastName: types.string,
  role: types.string,
});

const correctUser = 'admin';
const correctPassword = 'admin';

export const AuthStore = types
  .model({
    user: types.maybeNull(CurrentUser),
    isLoading: types.maybeNull(types.boolean),
    errorMessage: types.maybeNull(types.string),
  })
  .views((self) => ({
    get isUserAuthenticated() {
      return self.user !== null;
    },
    get errorWasRaised() {
      return self.errorMessage !== null;
    },
  }))
  .actions((self) => ({
    login: flow(function* login(
      username: string,
      password: string,
      generateError?: boolean
    ) {
      try {
        self.isLoading = true;
        yield new Promise((resolve, reject) => {
          if (generateError) {
            throw new Error('Simple error');
          }
          if (username !== correctUser || password !== correctPassword) {
            throw new Error('Credentials error');
          }
          setTimeout(() => resolve({ username: 'yeah', success: true }), 1000);
        });

        self.user = CurrentUser.create({
          username,
          firstName: 'asf',
          lastName: 'dalfa',
          role: 'hao',
        });
        self.errorMessage = null;
      } catch (error) {
        self.errorMessage = error.message;
      } finally {
        self.isLoading = false;
      }
    }),
  }));
/* eslint-enable no-param-reassign */
export type IAuthStore = Instance<typeof AuthStore>;
