import { ColumnModel } from 'tubular-common';
import { ITbSelection } from 'tubular-react-common';
import * as React from 'react';
import { DataGridHeaderCell } from './DataGridHeaderCell';
import { DataGridHeaderSelectionCell } from './DataGridHeaderSelectionCell';

interface DataGridHeaderProps {
  columns: ColumnModel[];
  sortColumn: (colName: string) => void;
  rowSelectionEnabled: boolean;
  selection?: ITbSelection;
  isLoading: boolean;
  rows: any[];
}

export const DataGridHeader: React.FunctionComponent<DataGridHeaderProps> = ({
  columns,
  sortColumn,
  rowSelectionEnabled,
  selection,
  isLoading,
  rows,
}: DataGridHeaderProps) => (
  <thead className="bg-gray-50">
    <tr role="rowheader" className="table table-fixed w-full">
      {rowSelectionEnabled && (
        <DataGridHeaderSelectionCell
          rows={rows}
          isLoading={isLoading}
          selection={selection}
        />
      )}
      {columns
        .filter((col) => col.visible)
        .map((col) => (
          <DataGridHeaderCell
            key={col.name}
            column={col}
            sortColumn={sortColumn}
          />
        ))}
    </tr>
  </thead>
);

export default DataGridHeader;
