import * as React from 'react';
import { ColumnModel } from 'tubular-common';
import tw from 'tailwind-styled-components';

const CheckboxCell = tw.td`
px-6
py-3
whitespace-nowrap
flex
justify-center
`;

export interface DataGridCheckboxCellProps {
  column: ColumnModel;
  checked: boolean;
  colIndex: number;
}

export const DataGridCheckboxCell: React.FunctionComponent<DataGridCheckboxCellProps> = ({
  column,
  checked,
  colIndex,
}: DataGridCheckboxCellProps) => (
  <CheckboxCell role="cell" key={column.name} colIndex={colIndex}>
    {checked && (
      <svg
        className="h-4 w-4"
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M5 13l4 4L19 7"
        />
      </svg>
    )}
  </CheckboxCell>
);
