import * as React from 'react';

import { Switch, Route } from 'react-router-dom';

import { observer } from 'mobx-react-lite';
import './tailwind.css';

import { useAuth } from '@hornero/stores';
import { CoreUsersPage, CoreUsersLoginPage } from '@hornero/core/users';
import { CoreTenantsPage } from '@hornero/core/tenants';
import { OverlayContainer } from '@hornero/overlay';

const App: React.FunctionComponent = observer(() => {
  const auth = useAuth();
  if (!auth.isUserAuthenticated) {
    return <CoreUsersLoginPage />;
  }
  return (
    <>
      <OverlayContainer />
      <Switch>
        <Route exact path="/">
          <CoreUsersPage />
        </Route>
        <Route path="/tenants">
          <CoreTenantsPage />
        </Route>
      </Switch>
    </>
  );
});

export default App;
