import {
  ColumnDataType,
  ColumnModel,
  parseDateColumnValue,
} from 'tubular-common';
import * as React from 'react';
import { DataGridCheckboxCell } from './DataGridCheckboxCell';
import { DataGridTextCell } from './DataGridTextCell';

const renderCell = (
  column: ColumnModel,
  value: any,
  colIndex: number
): React.ReactNode => {
  switch (column.dataType) {
    case ColumnDataType.Boolean:
      return (
        <DataGridCheckboxCell
          key={column.name}
          column={column}
          checked={!!value}
          colIndex={colIndex}
        />
      );
    case ColumnDataType.Numeric:
      return (
        <DataGridTextCell
          key={column.name}
          column={column}
          textAlign="text-right"
          text={value}
          colIndex={colIndex}
        />
      );
    case ColumnDataType.Date:
    case ColumnDataType.DateTime:
    case ColumnDataType.DateTimeUtc: {
      const dateAsString = !value ? '' : parseDateColumnValue(column, value);
      return (
        <DataGridTextCell
          key={column.name}
          column={column}
          text={dateAsString}
          colIndex={colIndex}
        />
      );
    }
    default:
      return (
        <DataGridTextCell
          key={column.name}
          column={column}
          text={value}
          colIndex={colIndex}
        />
      );
  }
};

export default renderCell;
