import React from 'react';
import InputErrorMessage from '../../atoms/InputErrorMessage/InputErrorMessage';
import Label from '../../atoms/Label/Label';
import StyledSelect from '../Select/StyledSelect';
import { SelectFormProps } from './SelectFormProps';

const StyledSelectForm: React.FC<SelectFormProps> = ({
  label,
  required,
  name,
  error,
  errorMessage,
  ...props
}) => (
    <>
      <Label required={required} label={label} htmlFor={name} />
      <StyledSelect name={name} {...props} />
      <InputErrorMessage error={error} errorMessage={errorMessage} />
    </>
  );

export default StyledSelectForm;
