import * as React from 'react';
import { Route, Switch, useRouteMatch } from 'react-router-dom';

import { CoreUserAddForm } from './CoreUserAddForm';
import { CoreUsersList } from './CoreUsersList';

export const CoreUsersRoutes = () => {
  const { path } = useRouteMatch();
  return (
    <Switch>
      <Route exact path={path}>
        <CoreUsersList />
      </Route>
      <Route path={`${path}/add`}>
        <CoreUserAddForm />
      </Route>
    </Switch>
  );
};

export default CoreUsersRoutes;
