import React from 'react';
import StyledButton from './StyledButton';
import { stylesByColor } from '../Utils/Colors';
import {ButtonProps} from './ButtonProps';

const Button: React.FC<ButtonProps> = ({
  color = 'main',
  variant = null,
  disabled = false,
  children,
  onClick,
  ...props
}) => (
  <StyledButton
    type="button"
    variant={variant}
    colorstyle={stylesByColor(color)}
    disabled={disabled}
    onClick={onClick}
    {...props}
  >
    {children}
  </StyledButton>
);

export default Button;
