import React from 'react';
import tw from 'tailwind-styled-components';

interface RadioButtonProps {
  value: string;
  label?: string;
  name?: string;
  id?: string;
  comment?: string;
  checked?: boolean;
  disabled?: boolean;
  onChange?: (event: any) => void;
}

const getStyle = ({ disabled, checked }: RadioButtonProps) => ` ${
  disabled ? 'opacity-30' : 'opacity-100'
}
    ${checked ? 'border-primary bg-primary' : 'border-gray-400'}`;

const RadioInput = tw.input`
opacity-0
absolute
`;

const RadioLabel = tw.label`
block 
text-black
text-base
${({ disabled }: RadioButtonProps) => disabled && 'opacity-30'}
`;

const RadioComment = tw.label`
text-xs 
text-gray-300
-mt-1
${({ checked }: RadioButtonProps) => (checked ? 'visible' : 'invisible')}
`;

const RadioLayout = tw.div`
flex
flex-row
items-start
my-0
`;

const TextContainer = tw.div`
flex
flex-col
items-start
ml-1
`;

const RadioContainer = tw.div`
mt-1
relative
bg-white
border
rounded-full
place-items-center
w-4
h-4
focus-within:ring-2
focus-within:ring-primary
focus-within:ring-offset-2
focus-within:outline-none
self-start
${getStyle}
`;

const CircleViewport = tw.svg`
text-white
fill-current
rounded
pointer-events-none
w-4
h-4
absolute
`;

const RadioButton: React.FC<RadioButtonProps> = ({
  name,
  value,
  label,
  id = value,
  comment,
  checked = false,
  disabled = false,
  onChange,
}) => (
  <RadioLayout>
    <RadioContainer checked={checked} disabled={disabled}>
      <RadioInput
        type="radio"
        id={id}
        name={name}
        value={value}
        onChange={onChange}
        disabled={disabled}
        defaultChecked={checked}
      />
      <CircleViewport viewBox="0.6 0.6 13 13">
        <circle cx="50%" cy="50%" r="3" fill="white" />
      </CircleViewport>
    </RadioContainer>
    <TextContainer>
      <RadioLabel htmlFor={id} disabled={disabled}>
        {label}
      </RadioLabel>
      <RadioComment checked={checked}>{comment}</RadioComment>
    </TextContainer>
  </RadioLayout>
);

export default RadioButton;
