import * as React from 'react';
import tw from 'tailwind-styled-components';

export interface QuickFilterModel {
  column: string;
  label: string;
  options: QuickFilterItem[];
}

export interface QuickFilterProps extends QuickFilterModel {
  onChange: (column: string, value: string) => void;
  cleared?: boolean;
}

interface QuickFilterItem {
  id: number;
  value: string;
}

const SelectFilter = tw.select`
border-0
bg-transparent
text-bold
flex-grow
self-stretch
appearance-none
pr-5
focus:outline-none
`;

const DivSelect = tw.div`
flex
flex-row
items-center
relative
mt-1 
block
bg-transparent
hover:bg-gray-200
`;

const Arrow = tw.svg`
absolute
text-gray-400
fill-current
right-0
py-1
w-5
h-5
pointer-events-none
`;

const QuickFilter: React.FC<QuickFilterProps> = ({
  column,
  label,
  options,
  onChange,
  cleared = false,
  ...props
}) => {
  const onChangeQuickFilter = (event: React.ChangeEvent<HTMLInputElement>) => {
    onChange(column, event.target.value);
  };

  return (
    <span className="inline-flex hover:bg-gray-200 p-1">
      <DivSelect>
        <label className="text-bold">{label}:</label>
        <Arrow viewBox="6 4 15 10">
          <path d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" />
        </Arrow>
        <SelectFilter onChange={onChangeQuickFilter} {...props}>
          <option
            data-testid="select-option-1"
            key={-1}
            value="All"
            selected={cleared}
          >
            All
          </option>
          {options.map((item: QuickFilterItem, index: number) => (
            <option
              data-testid={`select-option${index}`}
              key={item.id}
              value={item.value}
            >
              {item.value}
            </option>
          ))}
        </SelectFilter>
      </DivSelect>
    </span>
  );
};

export default QuickFilter;
