import { SimpleObservable } from 'uno-js';
import ModalItem from './ModalItem';
import {ModalItemProps} from './ModalItemProps';

class ModalService extends SimpleObservable {
  private items: ModalItem[] = [];

  public getItems() {
    return this.items;
  }

  public showModal(
    itemComponent: React.FunctionComponent<ModalItemProps>,
    props: ModalItemProps
  ) {
    return new Promise((resolve, reject) => {
      const item: ModalItem = new ModalItem();
      item.id = `id${new Date().valueOf().toString()}`;
      item.component = itemComponent;
      item.props = props;
      item.resolve = resolve;
      item.reject = reject;

      this.items.push(item);
      this.inform();
    });
  }

  public remove(modalId: string) {
    for (let i = 0; i < this.items.length; i++) {
      if (this.items[i].id === modalId) {
        this.items.splice(i, 1);
        this.inform();
        break;
      }
    }
  }
}

const modalService = new ModalService();
export default modalService;
