import React from 'react';
import styled from 'styled-components';
import { DataGrid } from '@hornero/tubular';
import {
  AggregateFunctions,
  ColumnDataType,
  ColumnSortDirection,
  createColumn,
} from 'tubular-common';
import { useTranslation } from 'react-i18next';

/* eslint-disable-next-line */
export interface TenantsProps {}

const StyledTenants = styled.div`
  color: pink;
`;

const dataSource = 'https://localhost:44397/api/tenants/paged';

const columns = [
  createColumn('TenantId', {
    dataType: ColumnDataType.Numeric,
    filterable: true,
    isKey: true,
    label: 'Id',
    sortDirection: ColumnSortDirection.Ascending,
    sortOrder: 1,
    sortable: true,
  }),
  createColumn('TenantName', {
    dataType: ColumnDataType.String,
    filterable: true,
    sortable: true,
  }),
  createColumn('TenantWebsite', {
    dataType: ColumnDataType.String,
    filterable: true,
    sortable: true,
  }),
  createColumn('TenantIdentifier', {
    dataType: ColumnDataType.String,
    filterable: true,
    sortable: true,
  }),
];

export function CoreTenantsList(props: TenantsProps) {
  const { t } = useTranslation(['users', 'common']);
  return (
    <StyledTenants>
      <h1>{t('tenants:Title')}</h1>
      <div className="h-96">
        <DataGrid
          gridName="tenants"
          columns={columns}
          dataSource={dataSource}
        />
      </div>
    </StyledTenants>
  );
}

export default CoreTenantsList;
