import React, { useState } from 'react';
import tw from 'tailwind-styled-components';
import {CheckboxProps} from './CheckboxProps';

const getStyle = (props: any) => {
  let style = '';
  if (props.disabled) {
    style += 'opacity-30';
  } else {
    style += 'opacity-100';
  }
  if (props.checked) {
    style += ' border-primary bg-primary';
  } else {
    style += ' border-gray-400';
  }
  return style;
};

const Checkbox = tw.label`
flex
flex-row
pb-2`;

const DivInput = tw.div`
bg-white
border
rounded
items-center
w-4
h-4
focus-within:ring-2
focus-within:ring-primary
focus-within:ring-offset-2
focus-within:outline-none
self-center
${getStyle}
`;

const Input = tw.input`
opacity-0
absolute`;

const CheckboxLabel = tw.span`
ml-2
text-gray-700`;

const SvgIcon = tw.svg`
text-white
fill-current
rounded
pointer-events-none
w-4
h-4`;

const DivContainer = tw.div`
flex
flex-col
relative
`;

const CheckboxContainer = tw.div`
flex
flex-col
relative
w-full
`;

const CheckboxComment = tw.label`
ml-2
text-xs
text-gray-300
absolute
-bottom-3
`;

const StyledCheckbox: React.FC<CheckboxProps> = ({
  disabled = false,
  label,
  id,
  defaultChecked = false,
  comment,
  ...props
}) => {
  const [isChecked, setIsChecked] = useState(defaultChecked);

  const onChecked = (event: any) => {
    setIsChecked(event.target.checked);
  };

  return (
    <DivContainer>
      <Checkbox>
        <DivInput disabled={disabled} checked={isChecked}>
          <Input
            id={id}
            type="checkbox"
            disabled={disabled}
            onClick={onChecked}
            defaultChecked={defaultChecked}
            {...props}
          />
          {isChecked && (
            <SvgIcon viewBox="2 3 14 12">
              <path d="M12.207 4.793a1 1 0 010 1.414l-5 5a1 1 0 01-1.414 0l-2-2a1 1 0 011.414-1.414L6.5 9.086l4.293-4.293a1 1 0 011.414 0z" />
            </SvgIcon>
          )}
        </DivInput>
        <CheckboxContainer>
          <CheckboxLabel htmlFor={id}>{label}</CheckboxLabel>
          <CheckboxComment>{comment}</CheckboxComment>
        </CheckboxContainer>
      </Checkbox>
    </DivContainer>
  );
};

export default StyledCheckbox;
