import * as React from 'react';
import { ColumnModel } from 'tubular-common';
import { ITbSelection } from 'tubular-react-common';
import { DataGridSelectCheckbox } from './DataGridSelectCheckbox';

export interface DataGridSelectionCellProps {
  selection?: ITbSelection;
  isLoading: boolean;
  row: any;
  columns: ColumnModel[];
}

export const DataGridSelectionCell: React.FunctionComponent<DataGridSelectionCellProps> = ({
  selection,
  isLoading,
  row,
  columns,
}: DataGridSelectionCellProps) => {
  const onChange = () => {
    selection.toggleRowSelection(row[columns.find((c) => c.isKey).name]);
  };
  const checked =
    selection.rowSelection[row[columns.find((c) => c.isKey).name]];
  return (
    <td className="group px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider w-1">
      <div className="relative inline-flex cursor-pointer">
        <DataGridSelectCheckbox
          checked={checked}
          disabled={isLoading}
          onChange={onChange}
        />
      </div>
    </td>
  );
};

export default DataGridSelectionCell;
