import {
  DataGridRowDefaultProps,
  DataGridSelectionCell,
  renderCell,
  TableRow,
} from '@hornero/tubular';
import React from 'react';
import { ActionsDataCell } from './ActionsDataCell';

const CustomRow: React.FunctionComponent<DataGridRowDefaultProps> = ({
  columns,
  row,
  rowSelectionEnabled,
  selection,
  isLoading,
  rowIndex,
}: DataGridRowDefaultProps) => (
  <TableRow rowIndex={rowIndex}>
    {rowSelectionEnabled && (
      <DataGridSelectionCell
        isLoading={isLoading}
        selection={selection}
        row={row}
        columns={columns}
      />
    )}
    {columns
      .filter((col) => col.visible)
      .map((col, index) => {
        if (col.name === 'Actions') {
          return (
            <ActionsDataCell key={col.name} column={col} colIndex={index} />
          );
        }
        return renderCell(col, row[col.name], index);
      })}
  </TableRow>
);

export default CustomRow;
