import React from 'react';
import InputErrorMessage from '../../atoms/InputErrorMessage/InputErrorMessage';
import Label from '../../atoms/Label/Label';
import TextInput from '../../atoms/TextInput/TextInput';
import {TextFormProps} from './TextFormProps';

const TextForm: React.FC<TextFormProps> = ({
  error = false,
  errorMessage,
  label,
  required = false,
  ...props
}) => (
  <>
    <Label required={required} label={label} />
    <TextInput error={error} {...props} />
    <InputErrorMessage error={error} errorMessage={errorMessage} />
  </>
);

export default TextForm;